	module.exports = {"data":{"post":{"id":"/root/project/content/posts/2017-11-15--downloading-large-zip-from-flickr/index.md absPath of file >>> MarkdownRemark","html":"<p>After going on an organized cycling trip, I attempted to download the <a href=\"https://www.flickr.com/photos/99699280@N07/sets/72157685533324160\">2.5GB photo archive from Flickr</a>. So many of the download tools that I used were not capable of downloading this frickin file on my Time Warner Cable 50Mbps connection.</p>\n<blockquote>\n<p>Frustrating, seemingly impossible.</p>\n</blockquote>\n<p>The tools I used that work fine with all other sites/files, just failed miserably with Flickr:</p>\n<ul>\n<li>Chrome</li>\n<li>FireFox</li>\n<li>XDM download manager</li>\n<li>FlashGet</li>\n<li>UGet</li>\n</ul>\n<p>I really wanted those photos so was not ready to give up. It is positive that all of the extremely annoyed Flickr users who raised an open <a href=\"https://www.flickr.com/help/forum/en-us/72157674951404393/\">issue</a> want their photos too.</p>\n<p>I came across <a href=\"https://ninjadownloadmanager.com/download-free\">Ninja Download Manager (free edition)</a>, this tool never actually failed with the download. Despite seemingly dropping out hundreds of times, it always recovered. Be patient, ignore the statistics you see when downloading and you’ll eventually get your archive out of Flickr.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/flickr-download-ninja-download-manager-25fb14d7932c6670ba6af1e970451547-3a66a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 563px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 144.404973357016%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"Screenshot of Ninja Download Manager\"\n        title=\"\"\n        src=\"/static/flickr-download-ninja-download-manager-25fb14d7932c6670ba6af1e970451547-3a66a.png\"\n        srcset=\"/static/flickr-download-ninja-download-manager-25fb14d7932c6670ba6af1e970451547-cc2ef.png 200w,\n/static/flickr-download-ninja-download-manager-25fb14d7932c6670ba6af1e970451547-9f735.png 400w,\n/static/flickr-download-ninja-download-manager-25fb14d7932c6670ba6af1e970451547-3a66a.png 563w\"\n        sizes=\"(max-width: 563px) 100vw, 563px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Keep in mind that you may have luck on a different internet connection. I had all this trouble with my Time Warner Cable 50Mbps connection YMMV. Using ethernet instead of wireless would definitely help too. I hope this post helps you out.</p>","fields":{"slug":"/downloading-large-zip-from-flickr/","prefix":"2017-11-15"},"frontmatter":{"title":"Downloading a large ZIP archive from Flickr","subTitle":"Have you also been frustrated when trying to download large ZIP archives on Flickr? Unless you have an amazing connection, it seems you'll be lucky to reach 23% downloaded before it fails! There is no doubt there is something seriously wrong with their web server. Luckily you can still get your files out, with the help of a really good modern download manager.","cover":{"childImageSharp":{"resize":{"src":"/static/aJZNErvd_400x400-694a17f503aae1b96ccf2b462595db29-160fa.png"}}}}},"author":{"id":"/root/project/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Paul S. Ness</strong> Software engineer with ten years of experience in a variety of industries such travel, payments, medical, fine art and publishing.</p>"},"footnote":{"id":"/root/project/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":""},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/downloading-large-zip-from-flickr/"}}

